<template>
  <div>
    <v-Mostrartodo v-model="show" :color="color" shaped center top>
      {{ text }}
    </v-Mostrartodo>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";

export default {
   computed: {
    ...mapGetters(["Mostrartodo"]),
    show: {
      get() {
        return this.Mostrartodo.show;
      },
      set(newShow) {
        const mostrars = {
          show: newShow,
          text: "",
          color: "",
        };
        this.show_Mostrar(mostrars);
      },
    },
    color() {
      return this.Mostrartodo.color;
    },
    text() {
      return this.Mostrartodo.text;
    },
  },
  methods: {
    ...mapActions(["show_Mostrar"]),
  },
};
</script>

